  .carousel-caption-bottom {
    color: #f2f2f2;
    font-size: 15px;
    padding: 8px 30px;
    width: 100%;
    position: absolute;
    top: 0px;
    width: 100%;
    text-shadow: 0px 0px 10px rgba(0,0,0,0.8);
  }
   .thumbnails::-webkit-scrollbar {
    display: none;
  }
  .thumbnail {
    margin: 0 5px;
  }
  .active-thumbnail {
    border: #476ff1 solid 3px;
  }
  @keyframes pause {
    0% {opacity: .2; }
    90% {opacity: 1; }
    
  }
  
  @keyframes fade {
    from {opacity: .4} 
    to {opacity: 1}
  }
  
  @media only screen and (max-width: 500px) {
    .prev,
    .next,
    .carousel-caption-bottom, 
    .carousel-caption-center, 
    .carousel-caption-top, 
    .slide-number 
    {
      font-size: 20px !important;
    }
    .dot {
      height: 4px;
      width: 4px;
    }
    .carousel-container{
      max-height: 250px !important;
    }
    .thumbnail {
      max-width: 70px;
    }
    .carousel-caption-bottom{
      bottom: 25px;
    }
  }

  
